.subscribe {
    &-outer {
        width: 100%;
        height: 24rem;
        transform: translateY(10rem);
    }
    
    &-inner {
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        height: 100%;
        border-radius: 2rem;
        background-color: $color-white;     
        box-shadow: 0px 118px 125px 4px rgba(0,0,0,0.16);
    }

    &__left {
        display: flex;
        flex-direction: column;
        flex-basis: 41rem;
        &-title {
            font-size: $fs-exl;
            font-weight: 500;
            line-height: 4.5rem;
            color: $color-black;
            margin-bottom: 2rem;
        }

        &-subtitle {
            color: $text-color-primary;
            font-size: $fs-md;
        }
    }

    &__right { 
        .linkButton {
            background-color: $color-primary;
            color: $color-white;
            box-shadow: 0px 2rem 2rem -.5rem rgba(245,56,56,0.35);

            &:hover {
                background-color: $color-white;
                color: $color-primary;
            }
        }
    }
}