.about {
    &-outer {
        width: 100%;
        height: 65rem;
    }
    
    &-inner {
        display: flex;
        align-items: center;
        height: 100%;
        padding: 0 5rem;
    }

    &-right {
        margin-left: auto;
        flex-basis: 38.5rem;

        &__title {
            font-weight: $fw-500;
            font-size: $fs-exl;
            line-height: 1.5;
            margin-bottom: 1rem;
        }

        &__subtitle {
            color: $text-color-primary;
            font-size: $fs-md;
            line-height: 2;
            margin-bottom: 1rem;
        }

        &__list {
            display: flex;
            flex-direction: column;
            &-item {
                display: flex;
                align-items: center;
                font-size: $fs-sm;
                &-bullet {
                    display: inline;
                }
                &:not(:last-child) {
                    margin-bottom: 1rem;
                }
            }
        }
    }
}