.globalSection {
    &-outer {
        width: 100%;
        height: 100rem;
    }

    &-inner {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-evenly;
    }

    &-title {
        text-align: center;
        width: 38rem;
        font-weight: $fw-500;
        font-size: $fs-exl;
        line-height: 1.5;
        color: $color-black;
    }

    &-subtitle {
        width: 55.5rem;
        text-align: center;
        font-size: $fs-md;
        line-height: 1.5;

        .bolderPart {
            font-weight: $fw-500;
        }
    }
}