.footer {
    &-outer {
        width: 100%;
        height: 56rem;
        background-color: #f8f8f8;
        overflow: hidden;
    }

    &-inner {
        width: 100%;
        height: 100%;
        display: flex;
    }

    &__left {
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding: 3rem;
        flex-basis: 50%;

        &-text {
            margin: 3rem 0;
            width: 34rem;
            color: $text-color-primary;
            font-size: $fs-md;
        }

        &-socials {
            transform: translateX(-2rem);
            display: flex;
        }

        &-copyright {
            color: #afb5c0;
            font-size: $fs-md;
        }
    }

    &__right {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-self: center;
        flex-basis: 50%;

        &-inner {
            width: 100%;
            display: flex;
            padding: 3rem;
        }

            &-col {
                display: flex;
                flex-direction: column;
                justify-content: center;
                flex-basis: 100%;
                height: 100%;

                .listWrap {
                    &__item {
                        margin: 1rem 0;
                        font-size: $fs-md;
                        
                        &-link {
                            color: $text-color-primary;
                            &:hover {
                                transition: $transition;
                                color: $color-black;
                                border-bottom: 1px solid $color-black  ;
                            }
                        }
                        &:first-child {
                            font-weight: $fw-500;
                            font-size: 1.8rem;
                            color: $color-black;
                        }            
                    }
                }
            }
    }
}