.plan {
    &-outer {
        width: 100%;
        height: 100rem;

        box-shadow: inset 0px 104px 125px 4px rgba(229,229,229,1);

    }
    
    &-inner {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
    }

    &-top {
        margin-top: 9.5rem;
        margin-bottom: 5rem;
        text-align: center;

        &__title {
            font-size: $fs-exl;
            font-weight: $fw-500;
            color: $color-black;
            margin-bottom: 3.5rem;
        }

        &__subtitle {
            font-size: $fs-md;
            color: $text-color-primary
        }
    }

    &-main {
        height: 100%;
        display: flex;
        justify-content: space-around;

        &__item {
            margin: 0 1rem;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: space-around;
            border: 2px solid #ddd;
            border-radius: 1rem;
            padding: 5rem;
            margin-bottom: 5rem;
            max-width: 33rem;
            width: 100%;

            &:hover {
                border-color: $color-primary;
                transition: $transition;
                cursor: pointer;
            }

            &-title {
                margin: 3rem 0;
                font-size: $fs-xl;
                font-weight: $fw-500;
            }

            &-list {
                min-height: 15rem;
                &-item {
                    display: flex;
                    align-items: center;
                    
                    &-img {
                        display: inline;
                        margin-right: 1rem;
                    }

                    &-text {
                        font-size: $fs-md;
                        color: $text-color-primary;
                    }
                }
            }

            &-tariff {
                &-price {
                    font-size: $fs-exl;
                    font-weight: $fw-500;
                    color: $color-black;
                }

                &-mo {
                    color: $text-color-primary;
                    font-size: $fs-exl;
                }
            }
        }
    }
}