.header {
    &-outer {
        width: 100%;
        height: 90rem;

        display: flex;
        flex-direction: column;
    }
    
    &-top {
        width: 100%;
        height: auto;
        padding-top: 4.5rem;

        &-inner {
            display: flex;
            justify-content: space-between;
            align-items: center;
        }

        .logo {
            &:hover {
                transition: $transition;
                opacity: .8;
            }
        }

        &__nav {
            display: flex;
            
            &-item {
                & + & {
                    margin-left: 4rem;
                }

                &-link {
                    color: $text-color-primary;
                    font-size: $fs-md;

                    &:hover {
                        transition: $transition;
                        color: $color-black;
                        border-bottom: 1px solid $color-black;
                    }
                }
            }
        }

        &__right {
            display: flex;
            align-items: center;

            &-signIn {
                font-size: $fs-md;
                margin-right: 3rem;
                color: $color-black;

                &-link {
                    color: $color-black;
                    &:hover {
                        transition: $transition;
                        color: $color-primary;
                    }
                }          
            }
        }
    }

    &-main {
        width: 100%;
        height: 100%;

        display: flex;
        justify-content: center;
        align-items: center;

        &-inner {
            display: flex;
            width: 100%;
            align-items: center;
            height: 100%;
        }

        &__left {
            flex-basis: 55.5rem;

            &-title {
                font-size: 5rem;
                font-weight: $fw-500;
            }

            &-subtitle {
                font-size: $fs-md;
                margin-top: 2rem;
            }

            &-getStarted {
                margin-top: 5rem;

                .linkButton {
                    width: 25rem;
                    border-radius: 1rem;
                    background-color: $color-primary;
                    color: $color-white;

                    box-shadow: 0px 2rem 2rem -.5rem rgba(245,56,56,0.35);

                    &:hover {
                        background-color: $color-white;
                        color: $color-primary;
                    }
                }
            }
        }
    }

    &-bottom {
        width: 100%;
        box-shadow: 0px 118px 125px 4px rgba(0,0,0,0.16);

        &-inner {
            display: flex;
            width: 100%;
            padding: 2rem 0;
        }

        &__item {
            display: flex;
            align-items: center;
            justify-content: center;
            flex-grow: 1;
            height: 12.5rem;
            
            &:not(:last-child) {
                border-right: 2px solid #EEEFF2;
            }

            &-info {
                margin-left: 3.5rem;
                flex-basis: 5.3rem;

                &-num {
                    color: $color-black;
                    font-weight: $fw-600;
                    font-size: $fs-xl;
                }

                &-text {
                    color: $text-color-primary;
                    font-size: $fs-l;
                }
            }
        }
    }
}