.sliderSection {
    &-outer {
        width: 100%;
        height: 60rem;
    }

    &-inner {
        display: flex;
        flex-direction: column;
    }

    &-top {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        text-align: center;
        margin-bottom: 5rem;

        &__title {
            width: 44.5rem;
            font-size: $fs-exl;
            font-weight: $fw-500;
            line-height: 1.5;
            margin-bottom: 2rem;
        }

        &__subtitle {
            width: 55.5rem;
            font-size: $fs-md;
            line-height: 1.5;
        }
    }

    &__item {
        flex-basis: 40rem;
        height: 23rem;
        border-radius: 1rem;
        overflow: hidden;

        &-top {
            width: 100%;
            display: flex;
            align-items: center;
            padding: 3rem 3rem 1rem 3rem;

            &-left {
                &-img {
                    margin-right: 2rem;
                }
            }

            &-center {
                &-fullName {
                    font-size: 1.8rem;
                    font-weight: $fw-500;
                }
            }

            &-right {
                margin-left: auto;
                font-size: $fs-md;

                &-img {
                    margin-left: .5rem;
                    display: inline-block;
                }
            }
        }

        &-content {
            padding: 0 3rem;
            font-size: $fs-md;
            line-height: 3rem;
        }
    }
}


.swiper {
    font-size: $fs-exl;
    color: $color-black;
    &-container {
        width: 100%;
        height: 100%;
        padding: 0 3rem;
    }

    &-wrapper {
        min-width: 100%;
        height: 30rem;
    }

    &-slide {
        border: 2px solid #ddd;

        &-active {
            border: 2px solid $color-primary;
        }
    }

    &-pagination {
        display: flex;
        width: 15rem;

        &-bullet {
            width: 1.5rem;
            height: 1.5rem;

            &-active {
                transition: $transition;
                background-color: $color-primary;
                width: 3.5rem;
                border-radius: 1rem;
            }
        }
    }

    &-nav {
        display: inline-block;
        position: absolute;
        top: 24rem;
        right: 1rem;
        z-index: 100;

        .swiper-button-nextCustom {
            width: 6rem;
            height: 6rem;
            border-radius: 50%;
            border: 2px solid $color-primary;
            margin-left: 2rem;
            background-color: $color-white;
            color: $color-primary;
            cursor: pointer;
            
            &::before {
                content: '→';
                display: inline-block;
                transform: translateY(-.2rem);
                font-size: $fs-exl;
            }

            &:focus {
                transition: $transition;
                background-color: $color-primary;
                outline: none;
                color: $color-white;
            }

            &:hover {
                transition: $transition;
                background-color: $color-primary;
                color: $color-white;
            }
        }
        
        .swiper-button-prevCustom {
            width: 6rem;
            height: 6rem;
            border-radius: 50%;
            border: 2px solid $color-primary;
            background-color: $color-white;
            color: $color-primary;
            cursor: pointer;
            
            &::before {
                content: '←';
                display: inline-block;
                transform: translateY(-.2rem);
                font-size: $fs-exl;
            }
            
            &:focus {
                transition: $transition;
                outline: none;
                color: $color-white;
                background-color: $color-primary !important;
            }

            &:hover {
                transition: $transition;
                background-color: $color-primary;
                color: $color-white;
            }
        }
    }
}