.container {
    max-width: 1320px;
    margin: 0 auto;
}

*,
*::after,
*::before {
    box-sizing: border-box;
}
* {scroll-behavior: smooth;}

html {font-size: 10px; font-family: 'Rubik', sans-serif;}

a {text-decoration: none;}
.bolderText {font-weight: $fw-500;}

// LinkButton
.linkButton {
    display: inline-block;
    width: 15rem;
    padding: 1.6rem;
    text-align: center;
    
    border: 1px solid $color-primary;
    border-radius: 5rem;

    font-size: $fs-md;
    font-weight: $fw-500;
    color: $color-primary;


    &:hover {
        color: $color-white;
        background-color: $color-primary;
        transition: $transition;
    }
}
